import { request } from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			order:[],
			express_name:'',
			express_count:'',
			express_code:'',
			is_show_express_code:0,
		};
	},
	onLoad(options){
		this.getInfo(options.id);
	},
	methods:{
		//获取列表
		getInfo(order_id){
			let that = this;
			request({
				url :'index.php?s=/wap/order/orderDetail',
				data:{
					orderId:order_id
				}
			}).then(function(res){
				that.order = res.data.data.order;
				that.express_count = res.data.data.express_count;
				that.express_name = res.data.data.express_name;
				that.express_code = res.data.data.express_code;
				that.is_show_express_code = res.data.data.is_show_express_code;
			});
		},
		//订单确认收货
		OrderTakeDelivery(order_id){
			let that = this;
			uni.showModal({
				content: '确认收到货品了吗？',
				success: res => {
					if(res.confirm) {
						request({
							url:'index.php?s=/wap/order/OrderTakeDelivery',
							method:"POST",
							data:{order_id:order_id},
						}).then(res=>{
							if(res.data>0){
								uni.showToast({
									title:"确认收货成功!",
									icon:"none",
								});
								that.getInfo();
							}else{
								uni.showToast({
									title:"操作失败!",
									icon:"none",
								});
							}
						});
					}
				}
			})
		},
	}
}
